// Singleton manager for synchronized auto-scroll animations
class AutoScrollManager {
  constructor() {
    this.subscribers = new Set();
    this.isAnimating = false;
    this.animationTimer = null;
    this.pauseTimer = null;
    
    // Timing configuration
    this.ANIMATION_DURATION = 8000; // 8 seconds for scroll animation
    this.PAUSE_DURATION = 3000; // 3 seconds pause at start and end
    this.CYCLE_DURATION = this.ANIMATION_DURATION + (this.PAUSE_DURATION * 2); // Total cycle
    
    this.startCycle();
  }

  subscribe(callback) {
    this.subscribers.add(callback);
    
    // Immediately notify new subscriber of current state
    callback({ isAnimating: this.isAnimating });
    
    return () => {
      this.subscribers.delete(callback);
    };
  }

  notify(state) {
    this.subscribers.forEach(callback => {
      try {
        callback(state);
      } catch (error) {
        console.error('AutoScroll subscriber error:', error);
      }
    });
  }

  startCycle() {
    const runCycle = () => {
      // Start with pause
      this.isAnimating = false;
      this.notify({ isAnimating: false });
      
      // Wait for initial pause, then start animation
      this.pauseTimer = setTimeout(() => {
        this.isAnimating = true;
        this.notify({ isAnimating: true });
        
        // Run animation, then pause again
        this.animationTimer = setTimeout(() => {
          this.isAnimating = false;
          this.notify({ isAnimating: false });
          
          // Wait for end pause, then restart cycle
          this.pauseTimer = setTimeout(() => {
            runCycle();
          }, this.PAUSE_DURATION);
        }, this.ANIMATION_DURATION);
      }, this.PAUSE_DURATION);
    };
    
    runCycle();
  }

  destroy() {
    if (this.animationTimer) clearTimeout(this.animationTimer);
    if (this.pauseTimer) clearTimeout(this.pauseTimer);
    this.subscribers.clear();
  }
}

// Create singleton instance
const autoScrollManager = new AutoScrollManager();

export default autoScrollManager;