import { useRef, useEffect, useState } from 'react';
import autoScrollManager from '../utils/autoScrollManager';
import './AutoScrollText.css';

const AutoScrollText = ({ children, className = '', pauseOnHover = true }) => {
  const containerRef = useRef(null);
  const textRef = useRef(null);
  const [shouldScroll, setShouldScroll] = useState(false);
  const [isGloballyAnimating, setIsGloballyAnimating] = useState(false);
  const [isLocallyPaused, setIsLocallyPaused] = useState(false);
  const [scrollDistance, setScrollDistance] = useState(0);
  const checkTimeoutRef = useRef(null);
  const resizeObserverRef = useRef(null);

  // Subscribe to global animation state
  useEffect(() => {
    const unsubscribe = autoScrollManager.subscribe((state) => {
      setIsGloballyAnimating(state.isAnimating);
    });

    return unsubscribe;
  }, []);

  // Check if text overflows and needs scrolling
  const checkOverflow = () => {
    if (containerRef.current && textRef.current) {
      const containerWidth = containerRef.current.getBoundingClientRect().width;
      const textWidth = textRef.current.scrollWidth;
      
      const needsScroll = textWidth > containerWidth + 5; // 5px buffer
      setShouldScroll(needsScroll);
      
      if (needsScroll) {
        const distance = textWidth - containerWidth;
        setScrollDistance(distance);
      }
    }
  };

  // Aggressive overflow checking with multiple methods
  useEffect(() => {
    // Clear any existing timeout
    if (checkTimeoutRef.current) {
      clearTimeout(checkTimeoutRef.current);
    }

    // Immediate check
    checkOverflow();

    // Delayed checks to handle rendering delays
    const timeouts = [10, 50, 100, 200, 500].map(delay =>
      setTimeout(checkOverflow, delay)
    );

    // ResizeObserver for container size changes
    if (resizeObserverRef.current) {
      resizeObserverRef.current.disconnect();
    }

    resizeObserverRef.current = new ResizeObserver(() => {
      checkOverflow();
    });

    if (containerRef.current) {
      resizeObserverRef.current.observe(containerRef.current);
    }

    // MutationObserver for content changes
    const mutationObserver = new MutationObserver(() => {
      checkOverflow();
    });

    if (textRef.current) {
      mutationObserver.observe(textRef.current, {
        childList: true,
        subtree: true,
        characterData: true,
      });
    }

    // Check on window resize
    const handleResize = () => {
      if (checkTimeoutRef.current) {
        clearTimeout(checkTimeoutRef.current);
      }
      checkTimeoutRef.current = setTimeout(checkOverflow, 100);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      timeouts.forEach(clearTimeout);
      if (checkTimeoutRef.current) {
        clearTimeout(checkTimeoutRef.current);
      }
      if (resizeObserverRef.current) {
        resizeObserverRef.current.disconnect();
      }
      mutationObserver.disconnect();
      window.removeEventListener('resize', handleResize);
    };
  }, [children]); // Re-run when children change

  // Also check when component mounts or updates
  useEffect(() => {
    const rafId = requestAnimationFrame(() => {
      checkOverflow();
    });
    return () => cancelAnimationFrame(rafId);
  });

  const handleMouseEnter = () => {
    if (pauseOnHover && shouldScroll) {
      setIsLocallyPaused(true);
    }
  };

  const handleMouseLeave = () => {
    if (pauseOnHover) {
      setIsLocallyPaused(false);
    }
  };

  // Determine if animation should be active
  const isAnimating = shouldScroll && isGloballyAnimating && !isLocallyPaused;

  return (
    <div 
      ref={containerRef}
      className={`auto-scroll-container ${className}`}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <div 
        ref={textRef}
        className={`auto-scroll-text ${shouldScroll ? 'scrollable' : ''} ${isAnimating ? 'animating' : ''}`}
        style={{
          '--scroll-distance': shouldScroll ? `-${scrollDistance}px` : '0px'
        }}
      >
        {children}
      </div>
    </div>
  );
};

export default AutoScrollText;